*, html {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.container, header {
  text-align: center;
}

.navbar{
  display: flex;
  justify-content: flex-end;
  background: #003648;
  padding: 20px;
}

.navbarlinks{
  width: 50%;
}

.navbarlinks a{
  padding-right: 10px;
}

.navbarlogout{
  width: 50%;
  float: right;
  text-align: right;
}

nav {
  display: flex;
  align-items: center;
  justify-content: space-around;
  padding: 20px;
  background-color: #003648;
}

nav h3 {
  color: white;
}

nav a {
  font-size: 20px;
  font-style: italic;
}

header p{
  font-style: italic;
  padding: 20px;
}

header p a {
  font-weight: 900;
}

.app{

}

.background{
  /* height: 100%; */
  position: fixed;
  z-index: -1;
  width: 100%;
  right: 0;
  bottom: 0;
  background-position: center;
}
